import React, {useEffect, useState} from 'react';
import axios from 'axios';
import Button from 'react-bootstrap/Button';
import DatePicker, { registerLocale, setDefaultLocale } from "react-datepicker";
import { ko } from "date-fns/esm/locale";
import {setMinutes, setHours} from "date-fns";
import { validPhoneNo, leftPad, toStringByFormatting } from '../utils/commonFuction';
import Footer from './common/Footer';

registerLocale('ko', ko);

const Reservation = (props) => {

    // 
    const [tncCheck, setTncCheck] = useState(false);
    const [soCheck, setSoCheck] = useState(false);
  
    // 
    const [visitFactory, setVisitFactory] = useState();
    const [visitPurpose, setVisitPurpose] = useState();
    
    // 
    const [meetingSpot, setMeetingSpot] = useState();
    const [visitDate, setVisitDate] = useState(new Date());         
    const [startTime, setStartTime] = useState(null);
    const [endTime, setEndTime] = useState(null);
    const [visitorCnt, setVisitorCnt] = useState(0);
    const [visitorTeam, setVisitorTeam] = useState();
    const [visitorPosition, setVisitorPosition] = useState();
    const [visitorName, setVisitorName] = useState();
    const [visitorTel1, setVisitorTel1] = useState();
    const [visitorTel2, setVisitorTel2] = useState();
    const [hasCar, setHasCar] = useState('');
    const [carNo, setCarNo] = useState();
    const [remarks, setRemarks] = useState();
  
    // 
    const [empNo, setEmpNo] = useState();
    const [empName, setEmpName] = useState();
    const [mngTel1, setMngTel1] = useState();
    const [mngTel2, setMngTel2] = useState();
  
    const [startTimeObj, setStartTimeObj] = useState();
    useEffect(()=>{
     
    })
    const [endTimeObj, setEndTimeObj] = useState({sTime:0, sHour:0, sMinute:0, tmpDateObj:0});
  
    const now = new Date();
  
    return(
      <>
      <section className="py-5">
        <div className="container mt-5">
          <div className="form-group">
            <div className="privacy-wrap">
              <p className="c-sub-title">     </p>
              <div className="privacy-content">
                <dl>
                  <dt>1.     </dt>
                  <dd>        ,             3   . </dd>
                  <dd>,       3   .</dd>
                  <dd>1)        </dd>
                  <dd>2)        </dd>
                  <dd>  3     ,              ,       .</dd>
                </dl>
                <dl>
                  <dt>2.    </dt>
                  <dd>() ,      ,      . </dd>
                  <dd>() ,         ,    , ,   .</dd>
                  <dd>- : , .</dd>
                </dl>
                <dl>
                  <dt>3.      </dt>
                  <dd>   ()   ,     . ,                ( DB).</dd>
                  <dd>           -   .      ,     .</dd>
                </dl>
                <dl>
                  <dt>4.        </dt>
                  <dd>           .</dd>
                  <dd>,          ,            .</dd>
                </dl>
              </div>
            </div>
          </div>
          <div className="form-check float-end">
            <input type="checkbox" className="form-check-input" id="exampleCheck1" checked={tncCheck} onClick={()=>{setTncCheck(!tncCheck)}}/>
            <label className="form-check-label" htmlFor="exampleCheck1">    .</label>
          </div>
        </div>
        <div className="container mt-5">
          <div className="form-group">
            <div className="privacy-wrap">
              <p className="c-sub-title">  </p>
              <div className="privacy-content">
                <dl>
                  <dt>  </dt>
                  <dd><input type="checkbox"/>        (  ) ~~</dd>
                  <dd><input type="checkbox"/>       ~~</dd>
                  <dd><input type="checkbox"/>        ~~</dd>
                  <dd><input type="checkbox"/>    ~~</dd>
                  <dd><input type="checkbox"/>       ~~</dd>
                  <dd><input type="checkbox"/>           ~~</dd>
                  {/* <dd className="ess">            .</dd> */}
                </dl>
              </div>
            </div>
          </div>
          <div className="form-check float-end">
            <input type="checkbox" className="form-check-input" id="exampleCheck2" checked={soCheck} onClick={()=>{setSoCheck(!soCheck)}}/>
            <label className="form-check-label" htmlFor="exampleCheck2">   .</label>
          </div>
        </div>
  
        <div className="container">
          <div className="text-center pt-5">
            <h2></h2>
            <p>
              
            </p>
          </div>
          
          <div className="card">
            <div className="card-body">
              <form id="bookingForm" action="#" method="" className="needs-validation" novalidate autoComplete="off">
                <p className="card-text"> </p>
                <hr/>
                <div className="form-group">
                  <label> </label>
                  <div className="d-flex flex-row justify-content-between align-items-center">
                    <select className="form-control mr-1" autoComplete="off" onChange={(e)=> setVisitFactory(e.target.value)}>
                      <option defaultValue=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                    </select>
                  </div>
                  <p className="card-text"> </p>
                  <hr/>
                  <div className="form-group required">
                    <label htmlFor="inputName"><span className="ess">*</span></label>
                    <input type="text" className="form-control" placeholder="" required autoComplete="off" value={visitPurpose} onChange={(e)=>{setVisitPurpose(e.target.value)}}/>
                    <small className="form-text text-muted"></small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputName"></label>
                    <input type="text" className="form-control" placeholder="" required autoComplete="off" value={meetingSpot} onChange={(e)=>{setMeetingSpot(e.target.value)}}/>
                    <small className="form-text text-muted">ex) </small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputDate"><span className="ess">*</span></label>
                    <div className="d-flex flex-row justify-content-between align-items-center">
                      <DatePicker
                        locale={ko}
                        selected={visitDate}
                        onChange={(date) => setVisitDate(date)}
                        minDate={new Date()}
                        dateFormat="yyyy-MM-dd"
                        onFocus={e => e.target.blur()}
                      />
                      <div className="m-1 customField1"></div>
                      <DatePicker
                        selected={startTime}
                        onChange={(time) => {
                                              setStartTime(time);
                                              var sHour = (time.getHours() - 10) < 0 ? ('0' + time.getHours().toString()) : time.getHours();
                                              var sMinute =  (time.getMinutes() - 10) < 0 ? ('0' + time.getMinutes().toString()) : time.getMinutes();
                                              var sTime = sHour + ":" + sMinute;
                                              var tmpDateObj = new Date();
                                              tmpDateObj.setHours(sHour);
                                              tmpDateObj.setMinutes(sMinute);
                                              setStartTimeObj({sTime, sHour, sMinute, tmpDateObj});
                                              setEndTime(0);
                                            }}
                        showTimeSelect
                        showTimeSelectOnly
                        timeIntervals={15}
                        timeCaption=""
                        timeFormat='HH:mm'
                        dateFormat="HHmm"
                        onFocus={e => e.target.blur()}            
                      />
                      <div className="customField1"></div>
                      ~
                      <div className="customField1"></div>
                      { startTimeObj ? 
                      <DatePicker
                        selected={endTime}
                        onChange={(time)=>{
                                            setEndTime(time);
                                            var eHour = (time.getHours() - 10) < 0 ? ('0' + time.getHours().toString()) : time.getHours();
                                            var eMinute =  (time.getMinutes() - 10) < 0 ? ('0' + time.getMinutes().toString()) : time.getMinutes();
                                            var eTime = eHour + ":" + eMinute;
                                            var tmpDateObj = new Date();
                                            tmpDateObj.setHours(eHour);
                                            tmpDateObj.setMinutes(eMinute);
                                            setEndTimeObj({eTime, eHour, eMinute, tmpDateObj});
                                          }}
                        showTimeSelect
                        showTimeSelectOnly
                        minDate={now}
                        minTime={setHours(setMinutes(now, startTimeObj.sMinute), startTimeObj.sHour)}
                        maxTime={setHours(setMinutes(now, 45), 23)}
                        timeIntervals={15}
                        timeCaption=""
                        timeFormat='HH:mm'
                        dateFormat="HHmm"
                        onFocus={e => e.target.blur()}
                      />: <DatePicker showTimeSelect showTimeSelectOnly/>
                      }
                    </div>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputName"><span className="ess">*</span></label>
                    <select className="form-control mr-1" onChange={(e)=> setVisitorCnt(e.target.value)}>
                      <option defaultValue="" disabled selected></option>
                      <option value="1">1</option>
                      <option value="2">2</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                      <option value="7">7</option>
                      <option value="8">8</option>
                      <option value="9">9</option>
                      <option value="10">10</option>
                    </select>
                    <small className="form-text text-muted"></small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputName"><span className="ess">*</span></label>
                    <input type="text" className="form-control" placeholder="" required autoComplete="off" value={visitorTeam} onChange={(e)=>{setVisitorTeam(e.target.value)}}/>
                    <small className="form-text text-muted"></small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputName"></label>
                    <input type="text" className="form-control" placeholder="" required autoComplete="off"value={visitorPosition} onChange={(e)=>{setVisitorPosition(e.target.value)}}/>
                    <small className="form-text text-muted"></small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputName"><span className="ess">*</span></label>
                    <input type="text" className="form-control" placeholder="" required autoComplete="off" value={visitorName} onChange={(e)=>{setVisitorName(e.target.value)}}/>
                    <small className="form-text text-muted"></small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputDate"> <span className="ess">*</span></label>
                    <div className="d-flex flex-row justify-content-between align-items-center">
                      <input type="text" className="form-control" readOnly defaultValue="010"/>
                      &nbsp;-&nbsp;
                      <input type="text" className="form-control" maxlength="4" value={visitorTel1} onChange={(e) => validPhoneNo(e, setVisitorTel1)}/>
                      &nbsp;-&nbsp;
                      <input type="text" className="form-control" maxlength="4" value={visitorTel2} onChange={(e) => validPhoneNo(e, setVisitorTel2)}/>
                    </div>
                  </div>
                  <div className="form-group">
                    <label></label><br/>
                    <div className="form-check form-check-inline">
                      <input type="radio" className="form-check-input" id="" name="dd" defaultValue="type1" value={''} checked={hasCar === ''} onChange={(e)=>{setHasCar(e.target.value)}}/>
                      <label className="form-check-label" htmlFor=""></label>
                    </div>
                    <div className="form-check form-check-inline">
                      <input type="radio" className="form-check-input" id="" name="dd" defaultValue="type2" value={''} checked={hasCar === ''} onChange={(e)=>{setHasCar(e.target.value);setCarNo('')}}/>
                      <label className="form-check-label" htmlFor=""></label>
                    </div>
                  </div>
                  {
                    hasCar == '' ? 
                    
                    <div className="form-group">
                      <label htmlFor="inputName"></label>
                      <input type="text" className="form-control" placeholder="" required autoComplete="off" value={carNo} onChange={(e)=>{setCarNo(e.target.value)}}/>
                      <small className="form-text text-muted"></small>
                    </div>
                    
                    : null
                    
                  }
                  <div className="form-group">
                    <label htmlFor="textAreaRemark"></label>
                    <textarea className="form-control" rows="2" placeholder="" onChange={(e)=>{setRemarks(e.target.value)}}></textarea>
                  </div>
                  <p className="card-text"> </p>
                  <hr/>
                  <div className="form-group">
                    <label htmlFor="inputName"> <span className="ess">*</span></label>
                    <input type="text" className="form-control" placeholder="" required value={empNo} onChange={(e)=>{setEmpNo(e.target.value)}}/>
                    <small className="form-text text-muted"></small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputName"><span className="ess">*</span></label>
                    <input type="text" className="form-control" placeholder="" required value={empName} onChange={(e)=>{setEmpName(e.target.value)}}/>
                    <small className="form-text text-muted"></small>
                  </div>
                  <div className="form-group">
                    <label htmlFor="inputName"> <span className="ess">*</span></label>
                    <div className="d-flex flex-row justify-content-between align-items-center">
                      <input type="text" className="form-control" readOnly defaultValue="010"/>
                      &nbsp;-&nbsp;
                      <input type="text" className="form-control" maxlength="4" value={mngTel1} onChange={(e) => validPhoneNo(e, setMngTel1)}/>
                      &nbsp;-&nbsp;
                      <input type="text" className="form-control" maxlength="4" value={mngTel2} onChange={(e) => validPhoneNo(e, setMngTel2)}/>
                    </div>
                  </div>
                  <hr/>
                  <Button variant="dark" size="lg" style={{width: "100%"}}
                      onClick={(e)=>{
                        if(!tncCheck){
                          alert("     .");
                          return;
                        }
                        if(!soCheck){
                          alert("   .");
                          return;
                        }
                        if(!visitFactory){
                          alert("  .");
                          return;
                        }
                        if(!visitPurpose){
                          alert(" .");
                          return;
                        }
                        if(!visitDate || !startTime || !endTime){
                          alert(" .");
                          return;
                        }
                        if(!visitorCnt){
                          alert(" .");
                          return;
                        }
                        if(!visitorTeam){
                          alert(" .");
                          return;
                        }
                        if(!visitorName){
                          alert(" .");
                          return;
                        }
                        if(!visitorTel1 || !visitorTel2){
                          alert(" .");
                          return;
                        }else if(visitorTel1 && visitorTel2){
                          if(visitorTel1.length < 4 || visitorTel2.length < 4){
                            alert("  .");
                            return;
                          }
                        }
                        if(hasCar == ""){
                          if(!carNo){
                            alert(" .");
                            return;
                          }
                        }
                        if(!empNo){
                          alert("  .");
                          return;
                        }
                        if(!empName){
                          alert(" .");
                          return;
                        }
                        if(!mngTel1 || !mngTel2){
                          alert("  .");
                          return;
                        }else if(mngTel1 && mngTel2){
                          if(mngTel1.length < 4 || mngTel2.length < 4){
                            alert("  .");
                            return;
                          }
                        }
  
                        var visitDateStr = toStringByFormatting(visitDate);
                        var startTimeStr = startTimeObj.sTime;
                        var endTimeStr = endTimeObj.eTime;
                        var visitorTelStr = '010-' + visitorTel1 + "-" + visitorTel2;
                        var mngTelStr = '010-' + mngTel1 + "-" + mngTel2;
                        var registerState = "";
                        const today = new Date();
                        
                        const hours = ('0' + today.getHours()).slice(-2);
                        const minutes = ('0' + today.getMinutes()).slice(-2);
                        const seconds = ('0' + today.getSeconds()).slice(-2);
                        const registerDate = toStringByFormatting(today) + ' ' +  hours + ':' + minutes + ':' + seconds;
  
                        var setparam = () => {
                          var param ={
                            visitFactory,      //   
                            visitPurpose,      //*
                            meetingSpot,       //
                            visitDateStr,      //*
                            startTimeStr,      //  
                            endTimeStr,        //
                            visitorCnt,        //*
                            visitorTeam,       //*
                            visitorPosition,   //
                            visitorName,       //*
                            visitorTelStr,     // *
                            hasCar,            //
                            carNo,             // == "" ?  : 
                            remarks,           //
                            empNo,             // *
                            empName,           // *
                            mngTelStr,         // *
                            registerDate,      //
                            registerState      // 
                          }
                          return param;
                        }
                        
                        // 
                        console.log(setparam());
  
                        //server req
                        axios.post("/register", setparam())
                        .then((res)=>{
                          console.log(res);
                          document.location.href = '/'
                          alert(".");
                        })
                        .catch((error)=>{
                          console.log(error);
                        });
                      }}>
                      
                  </Button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
      <Footer></Footer>
      </>
    )
  }
  
  export default Reservation;